/*
---- Require libs/_reset.scss
*/
html, body, div, span, h1, h2, h3, h4, h5, h6, p, img,
a, ul, ol, li, sub, sup, pre, code, iframe, form, input, button, textarea,
select, option, legend, table, thead, tbody, tfoot, tr,
th, td, caption, section, main, footer, header, nav, aside,
article, blockquote, details, hgroup, figcaption, figure,
video, audio, picture {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

article, aside, details, figcaption, figure,
footer, header, main, hgroup, menu, nav, section {
  display: block;
}

html, body {
  font-size: 100%;
  size-adjust: 100%;
  height: 100%;
}

a, a:hover, a:visited {
  text-decoration: none;
}

ol, ul {
  list-style: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  margin: 0;
}

img {
  border: 0;
  vertical-align: top;
}

