:root {
  --font-main: "Open Sans", sans-serif;
  --color-bg: #1C1B21;
  --color-accent: #7A66CC;
  --color-light: #ffffff;
  --font-color-main: #ffffff;
}

/*
---- Require libs/_vars.scss
*/
@font-face {
  font-family: "iconfont";
  src: url("../fonts/iconfont.eot?rtseok");
  src: url("../fonts/iconfont.eot?rtseok#iefix") format("embedded-opentype"), url("../fonts/iconfont.ttf?rtseok") format("truetype"), url("../fonts/iconfont.woff?rtseok") format("woff"), url("../fonts/iconfont.svg?rtseok#iconfont") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "iconfont" !important;
  speak-as: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-arrow:before {
  content: "\e900";
}

.icon-facebook:before {
  content: "\e901";
}

.icon-twitter:before {
  content: "\e902";
}

.icon-youtube:before {
  content: "\e903";
}

.icon-instagram:before {
  content: "\e904";
}

.icon-spotify:before {
  content: "\e905";
}

.icon-apple:before {
  content: "\e906";
}

.icon-play:before {
  content: "\e907";
}

/*
---- Main settings
*/
body {
  background-color: var(--color-bg);
  font-family: var(--font-main);
  color: var(--font-color-main);
}

[class*=__container] {
  max-width: 71.25rem;
  margin: 0 auto;
  padding: 0 0.9375rem;
}

#app {
  display: grid;
  grid-template-rows: 1fr auto;
  height: 100%;
}

/*
---- Require includes/_header.scss
*/
.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 5;
  transition: background 0.3s ease 0s;
}
.header.is-scroll {
  background: rgba(122, 102, 204, 0.7);
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 59.375rem;
  margin: 0 auto;
  padding: 1.875rem 0.9375rem;
  transition: padding 0.3s ease 0s;
}
.header__inner.is-scroll {
  padding: 0.625rem 0.9375rem;
}
.header__logo {
  margin: 0 4.0625rem 0 0;
}
@media (max-width: 575.98px) {
  .header__logo img {
    width: 11.25rem;
  }
}
@media (max-width: 991.98px) {
  .header__nav {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    padding: 2.5rem;
    background: var(--color-bg);
    transform: translate(100%, 0);
    transition: transform 0.3s ease 0s;
    overflow: auto;
  }
  .header__nav.is-open {
    transform: translate(0, 0);
  }
}
.header__list {
  display: flex;
  align-items: center;
}
@media (max-width: 991.98px) {
  .header__list {
    flex-direction: column;
    align-items: flex-start;
  }
}
.header__item:not(:last-child) {
  margin: 0 3.125rem 0 0;
}
.header__link {
  position: relative;
  display: inline-block;
  padding: 0 0 0.3125rem 0;
  font-size: 1.125rem;
  font-weight: 600;
  color: inherit;
  transition: color 0.3s ease 0s;
}
@media (max-width: 991.98px) {
  .header__link {
    display: inline-block;
    padding: 0.625rem;
  }
}
.header__link::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 0.125rem;
  background: var(--color-light);
  transition: width 0.3s ease 0s;
}
.header__link:hover::after {
  width: 100%;
}
.header__burger {
  display: none;
}
@media (max-width: 991.98px) {
  .header__burger {
    position: relative;
    z-index: 15;
    display: grid;
    gap: 0.3125rem 0;
    margin: 0 -0.625rem 0 0;
    padding: 0.625rem;
    background: transparent;
    border: 0;
  }
}
.header__burger.is-active span:nth-child(1) {
  transform: translate(0, 8px) rotate(45deg);
}
.header__burger.is-active span:nth-child(2) {
  opacity: 0;
}
.header__burger.is-active span:nth-child(3) {
  transform: translate(0, -8px) rotate(-45deg);
}
.header__burger span {
  display: block;
  width: 1.875rem;
  height: 0.1875rem;
  border-radius: 0.1875rem 0;
  background: var(--font-color-main);
  transition: all 0.3s ease 0s;
}

/*
---- Require includes/_footer.scss
*/
.footer {
  padding: 5.625rem 0 2.5rem;
}
.footer__inner {
  display: grid;
  gap: 3.75rem 0;
  padding: 0 0.9375rem;
}
.footer__logo {
  justify-self: center;
}
.footer__contacts {
  justify-self: center;
}
.footer__row {
  display: grid;
  gap: 0.3125rem 1.875rem;
}
@media (min-width: 576px) {
  .footer__row {
    grid-template-columns: 4fr 6fr;
  }
}
.footer__row:not(:last-child) {
  margin: 0 0 0.625rem 0;
}
.footer__caption {
  font-weight: 600;
  text-transform: uppercase;
}
@media (min-width: 576px) {
  .footer__caption {
    font-size: 1.125rem;
    text-align: end;
  }
}
.footer__text {
  text-transform: uppercase;
}
@media (min-width: 576px) {
  .footer__text {
    font-size: 1.125rem;
  }
}
.footer__text[href] {
  color: inherit;
}
.footer__text[href]:hover {
  text-decoration: underline;
}
.footer__social {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 0.9375rem 1.875rem;
  margin: 0 auto;
}
.footer__col {
  display: grid;
  grid-auto-flow: column;
  gap: 0 1.875rem;
}
.footer__icon {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3.75rem;
  height: 3.75rem;
  border-radius: 50%;
  background: #2C2C2C;
  font-size: 1.5rem;
  color: inherit;
}
.footer__icon::after {
  content: "";
  display: block;
  position: absolute;
  width: 2.75rem;
  height: 2.75rem;
  border-radius: 50%;
  background: var(--color-accent);
}
.footer__icon i {
  position: relative;
  z-index: 5;
}
.footer__icon .icon-youtube {
  font-size: 1rem;
}
.footer__icon .icon-twitter {
  font-size: 1.25rem;
}
.footer__copyright {
  font-size: 0.875rem;
  text-transform: uppercase;
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
}

/*
---- Require includes/_global.scss
*/
.intro {
  position: relative;
  padding: 5.625rem 0 15rem 0;
  height: 23.125rem;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, #1C1B21 90%), url("../../assets/img/intro.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
@media (min-width: 576px) {
  .intro {
    padding: 7.5rem 0 17.5rem 0;
  }
}
@media (max-height: 767.98px) {
  .intro {
    padding: 7.5rem 0 11.25rem 0;
  }
}
@media (min-height: 360px) {
  .intro {
    height: 100vh;
  }
}
.intro__container {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.intro__text {
  margin: auto 0 0 0;
}
.intro__suptitle {
  margin: 0 0 0.625rem 0;
  font-size: 1.875rem;
  text-transform: capitalize;
  text-align: center;
}
@media (min-width: 576px) {
  .intro__suptitle {
    font-size: 2.25rem;
    text-align: start;
  }
}
@media (min-width: 992px) {
  .intro__suptitle {
    font-size: 3rem;
  }
}
.intro__title {
  margin: 0 0 3.125rem 0;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 1px #ffffff;
  font-size: 3.125rem;
  text-transform: capitalize;
  text-align: center;
}
@media (min-width: 576px) {
  .intro__title {
    font-size: 4.375rem;
    text-align: start;
  }
}
@media (min-width: 992px) {
  .intro__title {
    font-size: 6.25rem;
  }
}
.intro__player {
  display: flex;
  column-gap: 2.8125rem;
  align-items: center;
  height: 27px;
}
.intro__line {
  flex-grow: 1;
  height: 0.125rem;
  border: 0;
  background: var(--color-light);
}
.intro__time {
  font-size: 1.125rem;
  font-weight: 500;
}

.news {
  margin: -9.375rem 0 0 0;
}
@media (max-height: 500px) and (max-width: 991.98px) {
  .news {
    margin: -6.25rem 0 0 0;
  }
}
@media (max-height: 450px) and (max-width: 991.98px) {
  .news {
    margin: -3.125rem 0 0 0;
  }
}
@media (max-height: 400px) and (max-width: 991.98px) {
  .news {
    margin: 0 0 0 0;
  }
}
@media (max-height: 600px) and (min-width: 992px) {
  .news {
    margin: -6.25rem 0 0 0;
  }
}
@media (max-height: 500px) and (min-width: 992px) {
  .news {
    margin: -3.125rem 0 0 0;
  }
}
@media (max-height: 450px) and (min-width: 992px) {
  .news {
    margin: 0 0 0 0;
  }
}
.news__inner {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.875rem;
}
.news__card {
  position: relative;
  height: 18.75rem;
  border-radius: 1.25rem 0;
  overflow: hidden;
}
.news__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.news__title {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  padding: 1.25rem;
  background: rgba(122, 102, 204, 0.7);
  border-radius: 1.25rem 0;
  font-size: 1rem;
  font-weight: 500;
}